import styled from "styled-components";

import {
	colors, displayFontSizes, Margins, marginsCss, spacingMap,
} from "../styleguide";
import Typography from "../Typography/Typography";

import {
	StyledButtonGroupButtonProps,
} from "./ButtonGroup.types";

export const StyledButtonList = styled.ul<Margins>`
	${marginsCss};
	list-style-type: none;
	box-sizing: border-box;

	display: flex;
	flex-direction: row;
	align-items: flex-start;
	padding: 0;

	background: ${colors.white};
	border: 1px solid ${colors.defaultGray};
	border-radius: ${spacingMap.sm};

 	li:first-child > button {
		border-radius: ${spacingMap.sm} 0 0 ${spacingMap.sm};
	}

	li:last-child > button {
		border-radius: 0 ${spacingMap.sm} ${spacingMap.sm} 0;
	}

`;

export const ButtonText = styled(Typography)`
	padding: 0 ${spacingMap.xxs};
`;

export const IconWrapper = styled.span`
	width: 20px;
	height: 20px;
	padding-left: ${spacingMap.xs};
	padding-right: ${spacingMap.xs};
`;

export const StyledButtonListItem = styled.li<StyledButtonGroupButtonProps>`
	display: flex;
	align-items: center;
	flex: none;
	order: 1;
	flex-grow: 1;
	padding-right: 3px;

	background: ${colors.white};
	height: 100%;
	border-right: 1px solid ${colors.defaultGray};

	:first-of-type {
        border-radius: ${spacingMap.sm} 0 0 ${spacingMap.sm};
		border-right: 1px solid ${colors.defaultGray};
    }

	:last-of-type {
		border-radius: 0 ${spacingMap.sm} ${spacingMap.sm} 0;
		border: none;
    }

	&:hover,&:active {
		background: ${colors.subduedBlueHover};
		color: ${colors.midBlue};
		cursor: pointer;
	}

	${({ isSelected }) => isSelected && `
		background: ${colors.subduedBlueHover};
		color: ${colors.midBlue};
		cursor: pointer;
	`}

`;

export const StyledButton = styled.button<StyledButtonGroupButtonProps>`
	display: flex;
	align-items: center;
	flex: none;
	order: 1;
	flex-grow: 1;

	font-family: ${displayFontSizes.caption.$fontFamily};
	font-weight: ${displayFontSizes.caption.$fontWeight};
	font-size: ${displayFontSizes.caption.$fontSize};
	line-height: ${displayFontSizes.caption.lineHeight};
	height: ${({ buttonSize }) => buttonSize === "small" ? 32 : 40}px;

	border: none;
	color: ${colors.black};
	background-color: transparent;
	outline: none;

	&:focus {
		box-shadow: 0 0 0 2px ${colors.white}, 0 0 0 4px ${colors.midBlue};
	}
	&:hover,&:active {
		cursor: pointer;
	}

	${({ isSelected }) => isSelected && `
		color: ${colors.midBlue};
	`}

	&:disabled {
		color: ${colors.black};
		background-color: transparent;
		opacity: 0.33;
		cursor: not-allowed;
	 };

`;

